body {
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  letter-spacing: normal;
  line-height: 1.5em;
}

button {
  border: none;
}

.clearfix:before,
.clearfix:after {
  content:"";
  display:table;
}
.clearfix:after {
  clear:both;
}
.clearfix {
  zoom:1; /* For IE 6/7 (trigger hasLayout) */
}

.container {
  max-width: 960px;
  margin: 0 auto;
  padding: 0 10px;
}


@import 'components/x-player';
@import 'components/x-chat';
@import 'components/announcments';
@import 'components/main';
@import 'components/main-footer';

@media screen and (max-width: 960px) {
  .announcment, .schedule, .x-chat {
      width: 100%;
  }

  .main-video-control {
    display: none;
  }

  .main-footer__rights {
    margin: 25px 0 0 0;
  }
}
